body {
  background: linear-gradient(315deg, #65005e 3%, #3c84ce 38%, #30eee2 68%, #ff1919 98%) 0 0 / 400% 400% fixed;
  margin: auto;
  font-family: -apple-system, BlinkMacSystemFont, sans-serif;
  animation: gradient 15s infinite;
  overflow: auto;
}

@keyframes gradient {
  0% {
    background-position: 0 0;
  }

  50% {
    background-position: 100% 100%;
  }

  100% {
    background-position: 0 0;
  }
}

.wave {
  width: 200%;
  height: 12em;
  opacity: .8;
  z-index: -1;
  background: rgba(255, 255, 255, .25);
  border-radius: 1000% 1000% 0 0;
  animation: wave 10s linear -3s infinite;
  position: fixed;
  bottom: 0;
  left: 0;
  transform: translate3d(0, 0, 0);
}

.wave:nth-of-type(2) {
  opacity: .8;
  animation: wave 18s linear infinite reverse;
  bottom: -1.25em;
}

.wave:nth-of-type(3) {
  opacity: .9;
  animation: wave 20s -1s infinite reverse;
  bottom: -2.5em;
}

@keyframes wave {
  2% {
    transform: translateX(1px);
  }

  25% {
    transform: translateX(-25%);
  }

  50% {
    transform: translateX(-50%);
  }

  75% {
    transform: translateX(-25%);
  }

  100% {
    transform: translateX(1px);
  }
}

